<template>
  <div class="food_container">
      <heade-top :head-title="headtitle" goBack="true"></heade-top>
      <section class="sort_container">
          <div class="sort_item" :class="{choose_type:sortBy=='foot'}">
              <div class="sort_item_container" @click="chooseType('foot')">
                  <div class="sort_item_border">
                      <span :class="{category_title:sortBy=='foot'}">
                          {{foodTitle}}
                      </span>
                      <svg width="10" height="10" xmlns="http://www.w3.org/2000/svg" version="1.1" class="sort_icon">
			    			<polygon points="0,3 10,3 5,8"/>
			    	    </svg>
                  </div>
              </div>
              <!-- transition vue过渡动画 -->
              <transition name="showlist" v-show="category">
                  <section v-show="sortBy=='foot'" class="category_container sort_detail_type">
                    <section class="category_left">
                        <ul>
                            <li v-for="(item,index) in category" class="category_left_li" :key="index" :class="{category_active:restaurant_category_id==item.id}" @click="selectCategoryName(item.id,index)">
                                <section>
                                    <img :src="getImgPath(item.image_url)" alt="" v-if="index" class="categroy_icon">
                                    <span>{{item.name}}</span>
                                </section>
                                 <section>
                                    <span class="category_count">{{item.count}}</span>
                                    <svg v-if="index" width="8" height="8" xmlns="http://www.w3.org/2000/svg" version="1.1" class="category_arrow" >
							    			<path d="M0 0 L6 4 L0 8"  stroke="#bbb" stroke-width="1" fill="none"/>
							    	</svg>
                                 </section>
                            </li>
                        </ul>
                    </section>
                        <section class="category_right">
                        <ul>
                            <li v-for="(item,index) in categoryDetail" v-if="index" :key="item.id" class="category_right_li" @click="getCategoryIds(item.id,item.name)" :class="{category_right_choosed: restaurant_category_ids == item.id || (!restaurant_category_ids)&&index == 0}">
                                <span>{{item.name}}</span>
                                <span>{{item.count}}</span>
                            </li>
                        </ul>
                    </section>
                  </section>
              </transition>
          </div>
           <div class="sort_item" :class="{choose_type:sortBy=='sort'}">
                <div class="sort_item_container" @click="chooseType('sort')">
                  <div class="sort_item_border">
                      <span :class="{category_title:sortBy=='sort'}">
                          排序
                      </span>
                      <svg width="10" height="10" xmlns="http://www.w3.org/2000/svg" version="1.1" class="sort_icon">
			    			<polygon points="0,3 10,3 5,8"/>
			    	    </svg>
                  </div>
                </div>
                <transition name="showlist">
                    <section v-show="sortBy=='sort'" class="sort_detail_type">
                        <ul class="sort_list_container" @click="sortList($event)">
                            <li class="sort_list_li">
                                <svg>
									<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#default"></use>
								</svg>
                                <p data="0" :class="{sort_select:sortByType==0}">
                                    <span>智能排序</span>
                                    <svg v-if="sortByType == 0">
										<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
									</svg>
                                </p>
                            </li>
                             <li class="sort_list_li">
                                <svg>
									<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#distance"></use>
								</svg>
                                <p data="5" :class="{sort_select:sortByType==5}">
                                    <span>距离最近</span>
                                    <svg v-if="sortByType == 5">
										<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
									</svg>
                                </p>
                            </li>
                            <li class="sort_list_li">
                               <svg>
									<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#hot"></use>
								</svg>
                                <p data="6" :class="{sort_select:sortByType==6}">
                                    <span>销量最高</span>
                                    <svg v-if="sortByType == 6">
										<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
									</svg>
                                </p>
                            </li>
                             <li class="sort_list_li">
                               <svg>
									<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#price"></use>
								</svg>
                                <p data="1" :class="{sort_select:sortByType==1}">
                                    <span>起送价低</span>
                                    <svg v-if="sortByType == 1">
										<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
									</svg>
                                </p>
                            </li>
                             <li class="sort_list_li">
                               <svg>
									<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#speed"></use>
								</svg>
                                <p data="2" :class="{sort_select:sortByType==2}">
                                    <span>速度最快</span>
                                    <svg v-if="sortByType == 2">
										<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
									</svg>
                                </p>
                            </li>
                             <li class="sort_list_li">
                               <svg>
									<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#rating"></use>
								</svg>
                                <p data="3" :class="{sort_select:sortByType==3}">
                                    <span>评分最高</span>
                                    <svg v-if="sortByType == 3">
										<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
									</svg>
                                </p>
                            </li>
                        </ul>
                    </section>
                </transition>
           </div>
           <div class="sort_item" :class="{choose_type:sortBy=='activity'}">
             <div class="sort_item_container" @click="chooseType('activity')">
                  <div class="sort_item_border">
                      <span :class="{category_title:sortBy=='activity'}">
                          筛选
                      </span>
                      <svg width="10" height="10" xmlns="http://www.w3.org/2000/svg" version="1.1" class="sort_icon">
			    			<polygon points="0,3 10,3 5,8"/>
			    	    </svg>
                  </div>
              </div>
              <transition name="showlist">
                  <section class="sort_detail_type filter_container" v-show="sortBy=='activity'">
                      <section style="width:100%">
                          <header class="filter_header_style">配送方式</header>
                          <ul class="filter_ul">
                              <li class="filter_li" v-for="item in Delivery" :key="item.id" @click="selectDeliveryMode(item.id)">
                                <svg :style="{opacity: (item.id == 0)&&(delivery_mode !== 0)? 0: 1}">
									<use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="delivery_mode == item.id? '#selected':'#fengniao'"></use>
								</svg>
                                <span :class="{selected_filter:delivery_mode==item.id}">{{item.text}}</span>
                              </li>
                          </ul>
                      </section>
                      <section style="width:100%">
                          <header class="filter_header_style">
                              商家属性(可以多选)
                          </header>
                          <ul class="filter_ul" style="paddingBottom:0.5rem">
                              <li v-for="(item,index) in Activity" class="filter_li" :key="item.id" @click="selectSupportIds(index,item.id)">
                                <svg v-show="support_ids[index].status" class="activity_svg">
										<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
								</svg>
                                <span class="filter_icon" :style="{color:'#'+item.icon_color,borderColor:'#'+item.icon_color}" v-show="!support_ids[index].status">
                                    {{item.icon_name}}
                                </span>
                                <span :class="{selected_filter:support_ids[index].status}">{{item.name}}</span>
                              </li>
                          </ul>
                      </section>
                      <footer class="confirm_filter">
                          <div class="clear_all filter_button_style" @click="clearSelect()">清空</div>
                          <div class="filter_button_style  confirm_select" @click="confirmSelectFun()">确定({{filterNum}})</div>
                      </footer>
                  </section>
              </transition>
           </div>
      </section>
      <transition name="showcover">
          <div class="back_over" v-show="sortBy"></div>
      </transition>
      <section class="shop_list_container">
          <shop-list :geohash="geohash" :restaurantCategoryId="restaurant_category_id" :restaurantCategoryIds="restaurant_category_ids" :sortByType="sortByType" :deliveryMode="delivery_mode" :confirmSelect="confirmStatus" :supportIds="support_ids" v-if="latitude" @DidConfrim="clear_kong"></shop-list>
      </section>
  </div>
</template>

<script>
import headeTop from '../../components/header/header';
import {mapState,mapMutations} from 'vuex';
import {msiteAddress,foodCategory,foodDelivery,foodActivity} from '../../service/getData'
import {getImgPath} from '../../components/common/mixin'
import shopList from '../../components/common/shoplist'
export default {
    data(){
        return{
            geohash:'',//city页面传过来的值
            headtitle:'',//头部标签值
            foodTitle:'',//排序左侧头部标签
            restaurant_category_id:'',//食品类型id
            restaurant_category_ids:'',//筛选类型id
            sortBy:'',//筛选条件
            category:null,//分类左侧数据
            categoryDetail:null,//分类右侧详细数据
            sortByType:null,//根据任何方式排序
            Delivery:null,//配送方式数据
            Activity:null,//商家支持活动数据
            delivery_mode:null,//选中的配送方式
            support_ids:[],//选中的商品活动列表
            filterNum:0,//所选中所有样式的集合
            confirmStatus:false//确认选择
        }
    },components: {
        headeTop,
        shopList
    },created () {
        this.initData();
    },methods: {
        ...mapMutations([
            'RECORD_ADDRESS'
        ]),
        async initData()
        {
            this.headtitle=this.$route.query.title;
            this.foodTitle=this.headtitle;
            this.geohash=this.$route.query.geohash;
            this.restaurant_category_id=this.$route.query.restaurant_category_id
            //防止页面刷新vuex数据丢失重新获取
           if(!this.latitude)
           {
               let res=await msiteAddress(this.geohash);
               //记录当前经纬度到vuex里面
               this.RECORD_ADDRESS(res);
           }
           //获取左侧种类数据
            this.category=await foodCategory(this.latitude,this.longitude);
            //初始化分类时定位默认选项
            this.category.forEach(time=>{
                if(time.id==this.restaurant_category_id)
                {
                    this.categoryDetail=time.sub_categories;
                }
            });
            //获取筛选列表的配送方式
            this.Delivery= await foodDelivery(this.latitude,this.longitude);
            //获取筛选列表的商品活动
            this.Activity=await foodActivity(this.latitude,this.longitude);
            //记录support_ids状态默认不选中选中取反status为true时选中
            this.Activity.forEach((time,index)=>{
                this.support_ids[index]={status:false,id:time.id};
            })
        },
        //点击顶部三个选项展示不同的列表选中当前列表显示同时回收其他选项
        async chooseType(type)
        {
            if(this.sortBy!==type)
            {
                this.sortBy=type;
                if(type=='food')
                {
                    this.foodTitle='分类';
                }else{
                    //同步标题
                    this.foodTitle=this.headtitle;
                }
            }else{
                //再次点击相同的回收按钮
                this.sortBy='';
                if(type=='food')
                {
                    this.foodTitle=this.headtitle;
                }
            }
        },
        //分类左侧选中种类是右侧对应渲染
        selectCategoryName(id,index)
        {
            //第一个选项没有列表获取全部数据
            if(index==0)
            {
                this.restaurant_category_ids=null;
                this.sortBy='';
            }else{
                //不是第一个选项右侧显示数据
                this.restaurant_category_id=id;
                this.categoryDetail=this.category[index].sub_categories;
            }
        },
        //点击右侧某个选项时对数据从新渲染
        getCategoryIds(id,name)
        {
            this.restaurant_category_ids=id;
            this.sortBy='';
            this.foodTitle=this.headtitle=name;
        },
        sortList(event)
        {
            //event全局的可以通过event获取是哪个对象或者标签触发的
            let node;
            if(event.target.nodeName.toUpperCase() !== 'P')
            {
               node = event.target.parentNode;
            }else{
                node = event.target;
            }
            ////getAttribute返回指定属性名的属性值
            this.sortByType=node.getAttribute('data');
            this.sortBy='';
        },
        //筛选选项中的配送方式选者
        selectDeliveryMode(id){
            //当delivery_mode为空时让选中的配送方式filterNum加一
            if(this.delivery_mode==null)
            {
                this.filterNum++;
                this.delivery_mode=id;
            }
            //如果点击了相同的清空deliver_mode并让fillterNUm减一
            else if(this.delivery_mode==id)
            {
                this.filterNum--;
                this.delivery_mode=null;
            }else{
                this.delivery_mode=id;
            }
        },
        //点击商家活动取反
        selectSupportIds(index,id)
        {
            this.support_ids.splice(index,1,{status:!this.support_ids[index].status,id});
            this.filterNum=this.delivery_mode==null ? 0 :1;
            
            this.support_ids.forEach(item=>{
                if(item.status)
                {
                    this.filterNum++;
                }
            })
            
        },
        //清空数据
        clearSelect()
        {
            this.support_ids.map(item=>item.status=false);
            this.filterNum=0;
            this.delivery_mode=null;
        },
        //确定提交
        confirmSelectFun(){
            this.confirmStatus=!this.confirmStatus;
            this.sortBy='';
        },
        //点击确定时把已选状态清空
        clear_kong()
        {
            
            this.delivery_mode=null;
        }
    },computed: {
        ...mapState([
            'latitude','longitude'
        ])
    },mixins: [getImgPath]//mixins用来获取公共的方法指令
}
</script>

<style lang="scss" scoped>
    @import 'src/style/mixin';
    .food_container{
        margin-top: 3.6rem;
    }
    .back_over{
        @include wh(100%,100%);
        position: fixed;
        background:  rgba(0,0,0,0.3);
        z-index: 10;
    }
    .sort_container{
        background-color: #fff;
        border-bottom: 0.025rem solid #f1f1f1;
        position: fixed;
        top: 1.95rem;
        right: 0;
        width: 100%;
        display: flex;
        z-index: 13;
        box-sizing: border-box;
        //过度动画
        .showlist-enter-active,.showlist-leave-active{
            transition: all .3s;
        }
        .showlist-enter,.showlist-leave-active{
            opacity: 0;
            transform: translateY(-100%);
        }
        .sort_item{
            @include wh(33.3%,1.6rem);
            @include sc(0.55rem,#444);
            text-align: center;
            line-height: 1rem;
            .sort_item_container{
                @include wh(100%,100%);
                position: relative;
                z-index: 14;
                background: #fff;
                box-sizing: border-box;
                padding-top: .3rem;
                .sort_item_border{
                    height: 1rem;
                    border-right: 0.025rem solid $bc;
                }
            }
            .sort_detail_type{
                width: 100%;
                position: absolute;
                display: flex;
                top: 1.6rem;
                left:0;
                border-top: 0.025rem solid $bc;
                background: #fff;
                .sort_list_container{
                    width: 100%;
                    .sort_list_li{
                        height: 2.5rem;
                        display: flex;
                        align-items: center;
                        svg{
                            @include wh(0.7rem,0.7rem);
                            margin: 0.3rem 0.8rem;
                        }
                        p{
                            line-height: 2.5rem;
                            flex:auto;
                            text-align: left;
                            text-indent: 0.25rem;
                            border-bottom: 0.025rem solid $bc;
                            @include fj;
                            align-items: center;
                            span{
                                color:#666;
                            }
                        }
                        .sort_select{
                            span{
                                color:$blue;
                            }
                        }
                    }
                }
            }
            .filter_container{
                flex-direction: column;//纵向排列
                align-items: flex-start;//弹性项目靠前
                min-height: 10.6rem;
                background: #f1f1f1;
                .confirm_filter{
                    display: flex;
                    background: #f1f1f1;
                    width: 100%;
                    padding:.3rem .2rem;
                    .clear_all{
                        background: #fff;
                        border:0.025rem solid $bc;
                    }
                    .confirm_select{
                        background: #56d176;
                        color:#fff;
                        border:0.025rem solid #56d176;
                    }
                    .filter_button_style{
                       width: 45%;
                       padding:0.5rem;
                       margin-left: 0.5rem;
                       text-align: center;
                       border-radius: 0.5rem;
                       font-size: 0.8rem;
                    }
                }
                .filter_header_style{
                    @include sc(0.4rem,#333);
                    line-height: 1.5rem;
                    height: 1.5rem;
                    text-align: left;
                    padding-left: 0.5rem;
                    background: #fff;
                }
                .filter_ul{
                    display: flex;
                    flex-wrap: wrap;//弹性布局为多行如果溢出放再下一行
                    padding:0 0.5rem;
                    background:#fff;
                    .filter_li{
                        display: flex;
                        align-items: center;
                        border:0.025rem solid #eee;
                        @include wh(4.7rem,1.4rem);
                        margin-right: 0.25rem;
                        border-radius: 0.125rem;
                        padding:0 0.25rem;
                        margin-bottom: 0.25rem;
                        .selected_filter{
                            color:$blue;
                        }
                        .filter_icon{
                            @include wh(.8rem,.8rem);
                            font-size: 0.5rem;
                            border:0.025rem solid $bc;
                            border-radius: 0.15rem;
                            margin-right: 0.25rem;
                            line-height: .8rem;
                            text-align: center;
                        }
                        span{
                            @include sc(0.4rem,#333);
                        }
                        .activity_svg{
                            margin-left:0.25rem;
                        }
                        svg{
                            @include wh(.8rem,.8rem);
                            margin-right: 0.125rem;
                        }
                    }
                }
            }
            .category_container{
                .category_left{
                    flex:1;
                    background: #f1f1f1;
                    height: 16rem;
                    // 如果溢出框，则应该提供滚动机制。
                    overflow-y: auto;//overflow-y属性规定是否对内容的上/下边缘进行裁剪 - 如果溢出元素内容区域的话
                    .category_left_li{
                        span{
                            @include sc(0.5rem,#666);
                            line-height: 1.8rem;
                        }
                        @include fj;
                        padding: 0 0.5rem;
                        .categroy_icon{
                            @include wh(.8rem,.8rem);
                            vertical-align: middle;//vertical-align设置元素对齐方式middle把元素放在父元素的中部
                            margin-right: .2rem;
                        }
                        .category_count{
                            background: #ccc;
                            @include sc(.4rem,#fff);
                            padding: 0.1rem;
                            border:0.025rem solid #ccc;
                            border-radius: 0.8rem;
                            vertical-align: middle;
                            margin-right: 0.25rem;
                        }
                        .category_arrow{
                            vertical-align: middle;
                        }
                    }
                }
                .category_active{
                    background: #fff;
                }
                .category_right{
                    flex: 1;
                    background: #fff;
                    padding-left: 0.5rem;
                    height: 16rem;
                    overflow-y: auto;
                    .category_right_li{
                        @include fj;
                        height: 1.8rem;
                        line-height: 1.8rem;
                        padding-right: 0.5rem;
                        border-bottom: 0.025rem solid $bc;
                        span{
                            color:#666;
                        }
                    }
                    .category_right_choosed{
                        span{
                            color: $blue;
                        }
				    }
                }
            }
        }
        .choose_type{
            .sort_item_container{
                .category_title{
                    color:$blue;
                }
                .sort_icon{
                    fill: $blue;
                    transform: rotate(180deg);
                }
            }
        }
    }
    .showcover-enter-active,.showcover-leave-active{
        transition: opacity .3s;
    }
    .showcover-enter,.showcover-leave-active{
        opacity: 0;
    }
</style>



