<template>
    <div class="shoplist_container">
        <ul  v-if="shopListArr.length" type="1" v-load-more="loadMores">
            <router-link tag="li" :to="{path:'shop',query:{geohash,id:item.id}}" v-for="item in shopListArr" :key="item.id" class="shop_li">
                <section>
                    <img :src="imageBaseUrl+item.image_path" alt="" class="shop_img">
                </section>
                <hgroup class="shop_right">
                    <header class="shop_detail_header">
                        <h4 :class="item.is_premium? 'premium' : ''" class="shop_title ellipsis">
                            {{item.name}}
                        </h4>
                        <ul class="shop_detail_ul">
                            <li v-for="item in item.supports" :key="item.id" class="supports">
                                {{item.icon_name}}
                            </li>
                        </ul>
                    </header>
                    <h5 class="rating_order_num">
                        <section class="rating_order_num_left">
                            <section class="rating_section">
                                <!-- 评分星星组件 -->
                                <sating-star :rating='item.rating'></sating-star>
                                <span class="rating_num">{{item.rating}}</span>
                            </section>
                            <section class="order_section">
                                月销{{item.recent_order_num}}单
                            </section>
                        </section>
                        <section class="rating_order_num_right">
							<span class="delivery_style delivery_left" v-if="item.delivery_mode">{{item.delivery_mode.text}}</span>
							<span class="delivery_style delivery_right" v-if="zhunshi(item.supports)">准时达</span>
						</section>
                    </h5>
                    <h5 class="fee_distance">
                        <p class="fee">
                            ￥{{item.float_minimum_order_amount}}起送
                            <span class="segmentation"></span>
                            {{item.piecewise_agent_fee.tips}}
                        </p>
                        <p class="distance_time">
                            <!--toFixed四舍五入成小数位  -->
                            <span v-if="Number(item.distance)">{{item.distance>1000 ? (item.distance/1000).toFixed(2)+'km' : item.distance+'m'}}
                                <span class="segmentation"></span>
                            </span>
                            <span v-else>{{item.distance}}</span>
                            <span class="segmentation"></span>
                            <span class="order_time">{{item.order_lead_time}}</span>
                        </p>
                    </h5>
                </hgroup>
            </router-link>
        </ul>
        <ul v-else class="animation_opacity">
            <li class="list_back_li" v-for="item in 10" :key="item">
                <!-- 遮掩层img -->
                <img src="../../images/shopback.svg" alt="" class="list_back_svg">
            </li>
        </ul>
        <p class="empty_data" v-if="touchend">没有更多了</p>
        <aside class="return_top" @click="backTop" v-if="showBackStatus">
			<svg class="back_top_svg">
				<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#backtop"></use>
			</svg>
		</aside>
        <div class=" " ref="abc" style="background:red"></div>
        <transition name="loading">
            <loading v-show="showLoading"></loading>
        </transition>
    </div>
</template>

<script>
import {loadMore} from './mixin'
import {shopList} from '../../service/getData'
import {mapState} from 'vuex'
import {showBack,animate} from '../../config/mUtils'
import {imageBaseUrl} from '../../config/env'
import loading from './loading'
import satingStar from './satingStar'
export default {
    data(){
        return{
            setoff:0,//批次加载店铺列表，每次加载20个 limit=20
            shopListArr:[],//店铺列表数据
            preventRepeatReuqest:false,//到达底部加载数据防止重复加载
            showBackStatus:false,//显示返回顶部按钮
            showLoading:true,//显示加载动画
            touchend:false,//没有更多数据
            imageBaseUrl,
        }
    },
    computed: {
        ...mapState([
            'latitude','longitude'
        ])
    },
    components: {
        loading,satingStar
    },
    mounted () {
      this.initData();  
    },
    methods: {
       async initData(){
           
           let res=await shopList(this.latitude,this.longitude,this.setoff,this.restaurantCategoryId);
           this.shopListArr=[...res];
           if(res.length<20)
           {
               this.touchend=true;
           }
           console.log('完成')
           //显示加载动画
           this.hideLoading();
           //开始监听scrollTop的值,到达一定程度后显示回到顶部的值
           showBack(status=>{
               this.showBackStatus=status;
              
           })
       },
       //隐藏loading
       hideLoading()
       {
           this.showLoading=false;
       },
       //返回顶部
       backTop()
       {
           animate(document.body,{scrollTop:0},400,'ease-out');
       },
       //到达底部批次加载数据
       async loadMores()
       {
           if(this.touchend)
           {
               return;
           }
           //防止重复加载
           if(this.preventRepeatReuqest)
           {
               return;
           }
           this.showLoading=true;//加载动画
           this.preventRepeatReuqest=true;
           //数据定位加20
           this.setoff+=20;
           //再次获取
           let res=await shopList(this.latitude,this.longitude,this.setoff,this.restaurantCategoryId);
           this.hideLoading();
           this.shopListArr=[...this.shopListArr,...res];
           //当数据小于20的时候不需要再请求
           if(res.length<20)
           {
               this.touchend=true;
                return;
           }
           this.preventRepeatReuqest=false;
       },
       zhunshi(supports)
       {
           let zhunStatus;
           if(supports instanceof Array && supports.length)
           {
               supports.forEach(item=>{
                   if(item.icon_name==='准')
                   {
                       zhunStatus=true;
                   }
               })
           }else{
                zhunStatus=false;
            }
           return zhunStatus;
       },
       //监听父级传的新数据重新获取数据
       async listenPropChange()
       {
           this.showLoading=true;
           this.setoff=0;
           let res=await shopList(this.latitude,this.longitude,this.setoff,'',this.restaurantCategoryIds,this.sortByType,this.deliveryMode,this.supportIds);
           this.showLoading=false;
            this.shopListArr=[...res];
       }
    },mixins: [loadMore],//mixins加载指令
    props:['restaurantCategoryId','geohash','restaurantCategoryIds','sortByType','confirmSelect','deliveryMode','supportIds'],
    //根据监听父级传来的商品信息来获取选择种类的商品
    watch:{
        //监听父级的restaurantCategoryIds当值发生变化的时候从新获取数据用来排序和赛选
        restaurantCategoryIds:function(value)
        {
           // alert(value);
            this.listenPropChange();
        },
        //监听排序方式
        sortByType:function(value)
        {
            
            this.listenPropChange();
        },
        //监听父级确定按钮是否点击，并且返回一个自定义事件通知父级已经接受到数据此时父级才可以情空已选状态
        confirmSelect:function(value)
        {
            
            this.listenPropChange();
            
            this.$emit('DidConfrim');
            
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../style/mixin.scss';
.shoplist_container{
    background-color: #fff;
    margin-bottom: 2rem;

.shop_li{
    display: flex;
   border-bottom: 0.025rem solid #f1f1f1;
   padding: 0.7rem 0.4rem;
}
.shop_img{
    @include wh(2.7rem,2.7rem);
    display: block;
    margin-right: 0.4rem;
}
.list_back_li{
 height: 4.85rem;
    .list_back_svg{
        @include wh(100%,100%);
    }   
}
.shop_right{
    flex: auto;
    .shop_detail_header{
        @include fj;
        align-items: center;
        .shop_title{
            width: 8.5rem;
            color:#333;
            padding:0.01rem;
            @include font(0.65rem,0.65rem,'PingFangSC-Regular');
            font-weight: 700;
        }
        .premium::before{
            content:'品牌';
            display: inline-block;//内联对象
            font-size: 0.5rem;
            line-height: 0.6rem;
            color:#333;
            background-color: #ffd930;
            padding: 0 0.1rem;
            border-radius: 0.1rem;
            margin-right: 0.2rem;
        }
        .shop_detail_ul{
            display: flex;
            transform: scale(.8);
            margin-right: -0.3rem;
            .supports{
                @include sc(0.5rem,#999);
                border-bottom: 0.025rem sloid #f1f1ff;
                padding: 0 0.04rem;
                border-radius: 0.08rem;
                margin-left: 0.05rem;
            }
        }
    }
    .rating_order_num{
        @include fj(space-between);//平均分布前后留有一半空间
        height: 0.6rem;
        margin-top: 0.52rem;
        .rating_order_num_left{
            @include fj(flex-start);//像航头紧挨填充
            .rating_section{
                display: flex;
                .rating_num{
                    @include sc(0.4rem,#ff6000);
                    margin:0 0.2rem;
                }
            }
            .order_section{
                transform: scale(0.8);
                margin-left: -0.2rem;
                @include sc(0.4rem,#666);
            }
        }
        .rating_order_num_right{
            display: flex;
            align-items: center;
            transform: scale(.7);
            min-width: 5rem;
            justify-content: flex-end;//项目紧贴尾部
            margin-right: -0.8rem;
            .delivery_style{
                font-size: 0.4rem;
                padding:0.04rem 0.08rem 0;
                border-radius: 0.08rem;
                margin-left: 0.08rem;
                border:1px;
            }
            .delivery_left{
                color:#fff;
                background-color: $blue;
                border:0.025rem solid $blue;
            }
            .delivery_right{
                color:$blue;
                border:0.025rem solid $blue;
            }
        }
    }
    .fee_distance{
        margin-top: 0.52rem;
        @include fj;
        @include sc(0.5rem,#333);
        .fee{
            transform: scale(.9);
            @include sc(0.5rem,#666);
        }
        .distance_time{
            transform: scale(.9);
            span{
                color:#999;

            }
            .order_time{
                color:$blue;

            }
            .segmentation{
                color:#ccc;
            }
        }
    }

}
  .loader_more{
        @include font(0.6rem,3);
        text-align: center;
        color:#999;

    }
    .empty_data{
        @include sc(0.5rem,#666);
        text-align: center;
        line-height: 2rem;
    }
    .return_top{
        position: fixed;
        bottom: 3rem;
        right: 1rem;
        .back_top_svg{
            @include wh(2rem,2rem);
           
        }
    }
    .loading-enter-active, .loading-leave-active {
		transition: opacity 1s
	}
	.loading-enter, .loading-leave-active {
		opacity: 0
	}  
}
</style>



